import React, { Component } from "react";
const Footer = () => {
  return (
    <React.Fragment>
      <footer id="contact" className="footer-contact">
        <h1>Let's work together...</h1>
        <p>
          How do you take your coffee?
          <span className="heart fa fa-heart "> </span>
        </p>
        <div className="contact-links">
          <a
            href="https://web.facebook.com/anas.swalha"
            target="_blank"
            className="contact-details"
          >
            <i className="fab fa-facebook-square"></i> Facebook
          </a>

          <a
            href="mailto:anas.swalha@yahoo.com"
            target="_blank"
            className="contact-details"
            alt="anas.swalha@yahoo.com"
            id="popup"
          >
            <span className="popuptext">anas.swalha@yahoo.com</span>
            <i className="fas fa-at"></i> Send a mail
          </a>

          <a
            href="tel:077-261-3195"
            target="_blank"
            className="contact-details"
            id="popup"
          >
            <span className="popuptext">077-261-3195</span>
            <i className="fas fa-mobile-alt"></i> Call me
          </a>
        </div>
        <div className="presonal-info">
          <span className="fa fa-map-marker-alt"> Jubiha,Amman, Jordan</span>
          <span className="fa fa-birthday-cake"> Mar/25/1995</span>
        </div>
      </footer>
    </React.Fragment>
  );
};

export default Footer;
