import React, { Component } from "react";
import $, { data, easing } from "jquery";

class NavBar extends Component {
  componentDidMount() {
    $("#menu-icon-inside").click(function () {
      $(".nav-items").toggle(1000);
      if ($(this).hasClass("fa-times")) {
        $(this).removeClass("fa-times").addClass("fa-bars");
      } else {
        $(this).removeClass("fa-bars").addClass("fa-times");
      }
    });
  }
  render() {
    return (
      <React.Fragment>
        <header className="nav">
          <a id="menu-icon-inside" className="fa fa-times"></a>
          <nav className="nav-items">
            <div className="logo-wrapper">
              <h3 className="  logo-title">A.S</h3>
              <p className="  logo-name">Anas Sawalha</p>
              <h3 className="  logo-position">Web Developer</h3>
            </div>
            <a href="#experiences" className="experiences">
              Experiences
            </a>
            <a href="#skills" className="skills">
              Skills
            </a>
            <a href="#programming" className="programming">
              Programming Languages
            </a>
            <a href="#education" className="education">
              Education
            </a>
            <a href="#project" className="education">
              Projects
            </a>
            <a href="#contact" className="contact">
              Contact
            </a>
          </nav>
        </header>
      </React.Fragment>
    );
  }
}

export default NavBar;
