import React, { Component } from "react";
import markedEditro from "../imgs/markededitro.png";
import jsDoc from "../imgs/jsDoc.png";
import productPromo from "../imgs/productPromo.png";
import quoteM from "../imgs/quoteM.png";

class Projects extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="projects-grid project-tile">
          <a
            href="https://codepen.io/anas-sawalhah/full/OJydEqv"
            target="_blank"
            className="project-tile"
          >
            <img src={jsDoc} alt="" />
            <p>
              <span>Js Documentation</span>
            </p>
          </a>
          <a
            href="https://codepen.io/anas-sawalhah/full/PoZerBr"
            target="_blank"
          >
            <img src={quoteM} alt="" />
            <p>
              <span>Random Quote</span>
            </p>
          </a>
          <a
            href="https://codepen.io/anas-sawalhah/full/oNjJyGY"
            target="_blank"
          >
            <img src={productPromo} alt="" />
            <p>
              <span>Product Promotion</span>
            </p>
          </a>
          <a
            href="https://codepen.io/anas-sawalhah/full/mdVKqMG"
            target="_blank"
          >
            <img src={markedEditro} alt="" />
            <p>
              <span>Marked Editor</span>
            </p>
          </a>
        </div>
        <div className="show-all-box">
          <a
            className="show-all"
            href="https://codepen.io/anas-sawalhah"
            target="_blank"
          >
            SHOW ALL
          </a>
        </div>
      </React.Fragment>
    );
  }
}

export default Projects;
