import React, { Component } from "react";
class Skills extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="skill-box">
          <p>
            <span className="fa fa-keyboard"></span>10 fingers typewriting
          </p>
        </div>
        <div className="skill-box">
          <p>
            <span className="fa fa-question"></span>The ability of analysis and
            solve issues
          </p>
        </div>
        <div className="skill-box">
          <p>
            <span className="fa fa-code"></span>The ability to write maintain
            and reliable code
          </p>
        </div>
        <div className="skill-box">
          <p>
            <span className="fa fa-language"></span>
            Fluent in English language in writing, reading and conversations
          </p>
        </div>
        <div className="skill-box">
          <p>
            <span className="fa fa-book"> </span>Fast learner and smart worker
          </p>
        </div>
        <div className="skill-box">
          <p>
            <span className="fa fa-clock"></span>Time management
          </p>
        </div>
      </React.Fragment>
    );
  }
}

export default Skills;
